pre {
  display: block;
  background-color: #333;
  padding: 1em;
  border-radius: 0px;
  color: #ccc; }

pre .nocode {
  background-color: none;
  color: #000; }

pre .str {
  color: #ffa0a0; }

/* string  - pink */
