  pre {
    background-color: none; }
  pre .str, code .str {
    color: #060; }
  pre .kwd, code .kwd {
    color: #006;
    font-weight: bold; }
  pre .com, code .com {
    color: #600;
    font-style: italic; }
  pre .typ, code .typ {
    color: #404;
    font-weight: bold; }
  pre .lit, code .lit {
    color: #044; }
  pre .pun, code .pun {
    color: #440; }
  pre .pln, code .pln {
    color: #000; }
  pre .tag, code .tag {
    color: #006;
    font-weight: bold; }
  pre .atn, code .atn {
    color: #404; }
  pre .atv, code .atv {
    color: #060; } }

